import uniIcons from '../uni-icons/uni-icons.vue'
export default {
    name: 'UniSearchBar',
    components: {
        uniIcons
    },
    props: {
        placeholder: {
            type: String,
            default: '搜索'
        },
        radius: {
            type: [Number, String],
            default: 10
        },
        clearButton: {
            type: String,
            default: 'auto'
        }
    },
    data () {
        return {
            show: false,
            searchVal: ''
        }
    },
    watch: {
        searchVal () {
            this.$emit('input', {
                value: this.searchVal
            })
        }
    },
    methods: {
        searchClick () {
            this.searchVal = ''
            this.show = true
        },
        clear () {
            this.searchVal = ''
        },
        cancel () {
            this.$emit('cancel', {
                value: this.searchVal
            })
            this.searchVal = ''
            this.show = false
        },
        confirm () {
            this.$emit('confirm', {
                value: this.searchVal
            })
        }
    }
}
